












































	export default {
		data() {
			return {
				indicatorDots: true,
				indicatorColor: '#007aff',
				autoplay: true,
				interval: 5000,
				duration: 500,
				bannerData: [],
				navData: [
                    {
						img: '../../static/image/women.png',
						title: '女生榜'
					},
					{
						img: '../../static/image/man.png',
						title: '男生榜'
					},
					{
						img: '../../static/image/popular.png',
						title: '人气榜'
					},
					{
						img: '../../static/image/new.png',
						title: '新作榜'
					},
					{
						img: '../../static/image/rank.png',
						title: '追更榜'
					}
				],
				productData: []
			};
		},
		onLoad() {
            

            // 查看是否授权
            wx.getSetting({
                success (res){
                    if (!res.authSetting['scope.userInfo']) {
                        uni.reLaunch({
                        	url: '../auth/auth'
                        });
                    }
                }
            })


			uni.showLoading();
			this.getBanner();
			this.getProduct();
		},
		methods: {
			getBanner() {
				uni.request({
					url: this.$requestUrl + 'get_comic_banner',
					method: 'GET',
					success: res => {
						this.bannerData = res.data.data;
					},
					fail: () => {},
					complete: () => {}
				});
			},
			getProduct() {
				uni.request({
					url: this.$requestUrl + 'get_comic_by_release',
					method: 'GET',
					success: res => {
						this.productData = res.data.data;
					},
					fail: () => {},
					complete: () => {
						uni.hideLoading();
					}
				});
			},
			goList(e) {
				let detail = {
					release_type_id: e.release_type_id,
					release_type_name: e.release_type_name
				}
				uni.navigateTo({
					url: "../release/release?detailData=" + JSON.stringify(detail)
				})
			},
			goInfo(e) {
				let detail = {
					comic_id: e.id,
					title: e.title
				}
				uni.navigateTo({
					url: "../comic-info/comic-info?detailData=" + JSON.stringify(detail)
				})
			}
		},
		onShareAppMessage() {
			return {
				title: '漫画',
				path: '/pages/index/index'
			}
		},
	}



